import { Component, OnInit, ViewChild } from '@angular/core';
import { MatBottomSheet } from '@angular/material/bottom-sheet';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { BehaviorSubject } from 'rxjs';
import { AdmissionsService } from 'src/app/services/admissions.service';
import { ApplicationService } from 'src/app/services/application.service';
// import { SettingsService } from 'src/app/services/settings.service';
import { UtilityService } from 'src/app/services/utility.service';
import { PassMark } from '../../interfaces/students';
import { PassmarkPipe } from '../../pipes/passmark.pipe';
import { BottomSheetComponent } from '../../utilities/bottom-sheet/bottom-sheet.component';

@Component({
  selector: 'app-settings-passmark',
  templateUrl: './settings-passmark.component.html',
  styleUrls: ['./settings-passmark.component.css'],
  providers: [PassmarkPipe]
})
export class SettingsPassmarkComponent implements OnInit {

  displayedColumns: string[] = ['year', 'passmark', 'current', 'action'];

  statusList = [ 0, 1];



  title = '';
  dataSource: MatTableDataSource<PassMark> = new MatTableDataSource<PassMark>();
  statusChangeList: boolean[] = [];
  newEntry = false;
  selectedDish: Partial <PassMark> = {};
  filterValues = '';
  promptAnswer: BehaviorSubject<number> = new BehaviorSubject<number>(0);

  drinkAttribute = [];
  drinkType = [];


  // staffID: string;
  //   lName: string;
  //   fName: string;
  //   password: string;
  //   permissions: string;
  //   isActive: boolean;
  //   staffID_in: string;
  //   createStamp?: Date;


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
  constructor(
    // private drinkPipe: DrinkPipe,
    private passmarkPipe: PassmarkPipe,
    private userService: ApplicationService,
    private utilityService: UtilityService,
    private bottomSheet: MatBottomSheet,
    // private settingService: SettingsService

    ) {
      this.loadMenu();
      // settingService.loadDrinkAttribute();
      // settingService.loadDrinkType();
      this.loadSettings();

  }

  loadSettings(): void {


    // this.settingService.DrinkTypeList.subscribe((val) => {
    //   if (val) {
    //     this.drinkType = val;
    //   }
    // });


  }

  loadMenu(): void {
    this.userService.getPassmarkInfo();
    // this.barService.getMenu();
    this.userService.passMarkList.subscribe((val) => {
      // this.dataSource.data =  (val);
      this.dataSource.data =  (val);
      this.dataSource.sort = this.sort;
      this.dataSource.paginator = this.paginator;
      this.statusChangeList = [];
      for (let i = 0; i < this.dataSource.data.length; i++) {
        // this.paymentTypeList[i] = 'Walk-in';
        this.statusChangeList[i] = false;

    }


    });

  }

  setStatusOfChange(i: number, status: boolean): void {
    this.statusChangeList[i] = status;
  }

  getStatusOfChange(i: number): boolean {

    return this.statusChangeList[i];
  }

  // getDefValue(row: PassMark): boolean {
  //   let answer = false;
  //   if (row.notDefault)
  //    { answer= true; }
    // return answer;
    // var width = document.getElementById('foo').offsetWidth;
  // }

  // saveDish(): void {
  //   // this.barService.createDrink(this.selectedDish as Drink);

  // }

  reset(): void {
    if (this.newEntry) {
      const tempData = this.dataSource.data;
      tempData.splice(0, 1);
      this.statusChangeList.splice(0, 1);
      this.dataSource.data = tempData;
    }

    for (let i = 0; i < this.statusChangeList.length; i++) {
      this.statusChangeList[i] = false;
    }

    this.newEntry = false;
  }

  newDrink(): void {
    this.reset();

    const tempData = this.dataSource.data;
    this.selectedDish = {};
    tempData.splice(0, 0, this.selectedDish as PassMark);

    this.dataSource.data = tempData;
    this.dataSource.sort = this.sort;
    this.dataSource.paginator = this.paginator;
    for (let i = 0; i < this.dataSource.data.length; i++) {
      this.statusChangeList[i] = false;
    }
    this.newEntry = true;
    // this.doEdit('');
    this.setStatusOfChange(0, true);
    // this.loadMenu();

  }

  updateDrink(aUser: PassMark, index: number, toBeDeleted: boolean): void {
    this.selectedDish = aUser;

    // console.log("SELECTED DISH::::", this.selectedDish);


    if (this.newEntry) {

      if (!toBeDeleted) {

        this.userService.setPassmarkInfo(aUser as PassMark).subscribe(
          (data) => {
            if (data) {
              this.newEntry = false;
        this.reset();
        this.loadMenu();

            }
          }
        );


      }
      else {
        this.newEntry = false;
        if (this.filterValues === '' )
          {this.removeDrink(index); }
        else { this.loadMenu(); }
        this.reset();
      }

    }
    else {
      if (toBeDeleted) {
        this.promptAnswer.next(0);

        // const message = 'WARNING!! YOU ARE ABOUT TO DELETE A DISH! Continue?';
        this.openBottomSheet();
        // this.openSnackBar(message, 'close');
        this.promptAnswer.subscribe((val) => {
          if (val === 1) {
            this.userService.deletePassmarkInfo(this.selectedDish as PassMark).subscribe(
              (val2) => {
                if (val2) {
                  if (this.filterValues === '' ) {this.removeDrink(index); }
                  else {
                    this.loadMenu();
                  }

                }
              }
            );
          }
        });
      }
        else
      { this.userService.editPassmarkInfo(this.selectedDish as PassMark); }


    }
  }

  openBottomSheet(): void {
    const sheetRef =  this.bottomSheet.open(BottomSheetComponent, {
      // data: {name: 'User'}
    });
    sheetRef.afterDismissed().subscribe( data => {
      if (data && data.message === 'Cancel') {
      }
      if ( data && data.message === 'Status') {
        this.promptAnswer.next(1);
      }
    });
  }

  removeDrink(i: number): void {
    const temp = this.dataSource.data;
    this.dataSource.data = this.utilityService.sliceTable
    (i, this.paginator, temp);

  }


  // doEdit(NameRow: string): void {
  //   console.log('REST SETTINGS: ', NameRow );
  //   console.log('REST SETTINGS: ', this.dataSource.data );

  // }


  applyFilter(aFilterValue: string): void {
    // const filterValue = (event.target as HTMLInputElement).value;

    this.dataSource.filter = aFilterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}

